<script>
import axios from "axios";

import Header from "../components/Header.vue";
import Footer from "../components/Footer.vue";
export default {
  name: "Commandez",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      nom_complet: "",
      email: "",
      telephone: "",
      description: "",
      successMessage: "",
      errorMessage: "",
      image: {
        backgroundImage: "url(assets/img/breadcrumb/breadcurmb.webp)",
      },
    };
  },
  methods: {
    submitForm() {
      // Créez un objet FormData pour envoyer les données du formulaire à l'API
      const formData = new FormData();
      formData.append("nom_complet", this.nom_complet);
      formData.append("email", this.email);
      formData.append("telephone", this.telephone);
      formData.append("description", this.description);

      // Effectuez une requête POST à l'API avec les données du formulaire
      axios
        .post("https://opentek.000webhostapp.com", formData)
        .then((response) => {
          // Afficher le message de succès et vider le formulaire
          this.successMessage =
            "Votre commande a été validée avec succès! \nNous vous retournerons très vite via votre email ou numero de téléphone  .";
          this.nom_complet = "";
          this.email = "";
          this.telephone = "";
          this.description = "";
          setTimeout(() => {
            this.successMessage = "";
            this.errorMessage = "";
          }, 6000);
        })
        .catch((error) => {
          // Afficher le message d'erreur
          this.errorMessage =
            "Une erreur est survenue lors de l'enregistrement de la commande.";
          this.nom_complet = "";
          this.email = "";
          this.telephone = "";
          this.description = "";
          setTimeout(() => {
            this.successMessage = "";
            this.errorMessage = "";
          }, 6000);
        });
      this.nom_complet = "";
      this.email = "";
      this.telephone = "";
      this.description = "";
    },
    closeModal() {
      // Fermer la modal popup
      this.successMessage = "";
      this.errorMessage = "";
    },
  },
};
</script>
